export default {
    name: "pending",
    data(){
        return{
            show: false,
            shows: false,
            num: null,   // 包厢号
            all: false,    // 详情
            cont_id: null,   // 订单ID
            list: [],
            shop_id: null,
        }
    },
    created(){
        this.cont_id = this.$route.params.cont_id;
    },
    mounted(){
        this.getInfo();
    },
    methods:{
        getInfo(){
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.info,
                type: 'post',
                data:{
                    id: this.cont_id,
                },
                success: (response,allResponse) =>{
                    this.list = response.data;
                    this.num = response.weizhi;
                    this.shop_id = response.shop_id;
                }
            });
        },
        // 点击客人误操作=>显示弹窗
        fault(){
            this.show = true
        },
        // 点击 客人误操作=>显示弹窗=>确定按钮
        onConfirm () {
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.call_deal,
                type: 'post',
                data:{
                    id: this.cont_id,
                    status: 2,
                },
                success: (response,allResponse) =>{
                    // console.log(response)
                }
            });
            this.$router.push("/home")
        },
        // 点击服务完成=>显示弹窗
        over(){
            this.shows = true
        },
        // 点击服务完成=>显示弹窗=>点击确定
        onConfirms () {
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.call_deal,
                type: 'post',
                data:{
                    id: this.cont_id,
                    status: 1,
                },
                success: (response,allResponse) =>{
                    // console.log(response)
                }
            });
            this.$router.push({
                name: 'award',
                params:{
                    id: this.cont_id,
                    shop_id: this.shop_id,
                }
            })
        },
        // 订单详情弹窗
        allDetail(){
            this.all = !this.all;
        },
    }
}



